import React from 'react'
import styled from 'styled-components'

const Container = styled.section`
text-align:center;
background-color: #94B3FD;
padding:2rem;
@media only screen and (min-width: 768px){
p{
  font-size:1.75rem;
  padding: 0 6.5rem; 
}
}`

const About = () => {
  return (
    <Container id='4'>
      <h1>About Us</h1>
      <p>React is an open source javaScript library with was developed by facebook. One of the most loved framework</p>
    </Container>
  )
}

export default About
