import React, { useState } from 'react'
import styled from 'styled-components';
const Heading = styled.h1`
text-align:center;
padding:2rem 0`
const ImageStyling  = styled.img`
height:30vh;
width:100vw;
padding:1rem;
border-radius:30px;
object-fit:contain;
`
const Companies = () => {
  const [imageData,setImageData] = useState(Images);
  return(
    <>
    <Heading id='5' className='Section5'>Companies that use React.js</Heading>
    <div className='row'>
    {imageData.map((curElem)=>{
      const {id,url}= curElem
      return(
        <div className='cols' key={id}>
          <ImageStyling src={url} alt={`Company-image${id}`}/>
        </div>
      );
    })}
    </div>
    </>
  )
}

const Images = [{
  id:1,
  url:"./images/1.png"
},
{
  id:2,
  url:"./images/2.png"
},
{
  id:3,
  url:"./images/3.jpg"
},
{
  id:4,
  url:"./images/4.png"
},
{
  id:5,
  url:"./images/5.png"
},
{
  id:6,
  url:"./images/6.jpg"
},
{
  id:7,
  url:"./images/7.jpg"
}]

export default Companies
