import React from 'react'
import styled from 'styled-components'

const BackGround = styled.section`
height:70%;
background-color:#94B3FD`

const HeroSection = styled.section`
overflow-y:hidden;
display:grid;
place-items:center;
h1{
    padding:6rem 0 2rem 0;
    @media only screen and (min-width: 768px){
      font-size:3.5rem;
    }
    @media only screen and (min-width: 1280px){
      padding:15rem 0 2rem 0;
}
}
`

const ReactSection = styled.section`
text-align:center;
h1{font-size:10rem;
padding-top:5rem;
@media only screen and (min-width: 1280px){
      padding:12rem 0 0 0;
};
`

const ReactIcon =styled.i`
background-image: linear-gradient(71.18deg, rgb(0, 34, 255) -27.32%, rgb(0, 34, 255) -16.39%, rgb(81, 121, 254) -7.38%, rgb(165, 237, 182) 30.59%, rgb(250, 232, 90) 46.06%, rgb(253, 172, 62) 62.61%, rgb(255, 92, 0) 75.82%);
-webkit-background-clip:text;
color: transparent;
@media only screen and (min-width: 768px){
  font-size:20rem;
}
`
const Reuse = () => {
  return (
    <>
    <BackGround>
     <div className='row'>
        <div className='col-lg-6'>
        <HeroSection>
                <h1 className='Section1'>Hello There👋</h1>
                <h2>This is a React-built page</h2>
                </HeroSection>
        </div>
        <div className='col-lg-6 react-section'>
        <ReactSection>
        <h1><ReactIcon className="fa-brands fa-react"></ReactIcon></h1>
        </ReactSection>
        </div>
     </div>
     </BackGround> 
    </>
  )
}

export default Reuse
