import React from 'react'
import ReactJoyride,{BeaconRenderProps} from 'react-joyride'

const TourSteps = [{
    target:".Section1",
    content: "Welcome to React fan page"
},
{
    target:".Section2",
    content: "Create your own React app"
},
{
    target:".Section3",
    content: "React learning curve"
},
{
    target:".Section4",
    content: "React unique features"
},
{
    target:".Section5",
    content: "Companies that use react"
}]

const Tour = () => {
  return (
    <>
      <ReactJoyride steps={TourSteps} continuous={true} showSkipButton={true} showProgress={true} styles={{
        buttonNext:{
          backgroundColor:"green"
        }
      }}/>
    </>
  )
}

export default Tour
