import React from 'react'
import styled from 'styled-components'

const Container = styled.section`
padding:2rem;
text-align:center;
line-height:2;
`

const CodeEdit = styled.code`
padding:2rem 1rem;
border-radius:30px;
background-color:#16181d;
@media only screen and (min-width: 768px){
font-size:1.25rem;
}
`

const LearnMore = styled.section`
padding:2rem 0;
@media only screen and (min-width: 768px){
font-size:2rem;
}
`

const Documentation = () => {
  return (
    <>
    <Container id='1'>
    <LearnMore>
      <h1>Create React App</h1>
      <p>Run this command in terminal</p>
      </LearnMore>
      <CodeEdit className='Section2'>npx create-react-app my-app</CodeEdit>
      <LearnMore>
      <p>To learn react vist our official website</p>
      <a href='https://beta.reactjs.org/' target='_blank'>reactjs.org</a>
      </LearnMore>
      </Container>
    </>
  )
}

export default Documentation
