import React, { useState } from 'react'
import styled from 'styled-components';

const FeatureBlock = styled.div`
overflow-y:hidden;
padding:2rem 0;
height:20vh;
display:grid;
place-items:center;
text-align:center;
i{font-size:3rem;
  @media only screen and (min-width: 768px){
font-size:6rem;
@media only screen and (min-width: 1280px){
  padding:2rem 0 0 0;
}
}
}
p{
    font-size:1rem;
    padding:2.25rem 0 0 0;
    @media only screen and (min-width: 768px){
font-size:1.75rem;
@media only screen and (min-width: 1280px){
  padding:3rem 0 0 0;
}
}
`

const LinearColor = styled.i`
background-image: linear-gradient(71.18deg, rgb(0, 34, 255) -27.32%, rgb(0, 34, 255) -16.39%, rgb(81, 121, 254) -7.38%, rgb(165, 237, 182) 30.59%, rgb(250, 232, 90) 46.06%, rgb(253, 172, 62) 62.61%, rgb(255, 92, 0) 75.82%);
-webkit-background-clip:text;
color: transparent;
`
const Features = () => {
    const [featuresData,serFeaturesData]= useState(featuresList);
  return (
    <>
      <div className='row' id='3'>
      {featuresData.map((curElem)=>{
        const {id,icon,content}=curElem;
        return(
            <FeatureBlock className='col-lg-4' key={id}>
            <LinearColor className={`fa-solid ${icon}`}></LinearColor>
            <p className='Section4'>{content}</p>
            </FeatureBlock>
        );
      })}
      </div>
    </>
  )
}
const featuresList = [{
    id:1,icon:"fa-code",content:"React makes your code DRY"
},
{
    id:2,icon:"fa-recycle",content:"Create components reuse again"
},
{
    id:3,icon:"fa-users-line",content:"Huge open source community"
}]
export default Features
