import React from 'react'
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import styled from 'styled-components';

const Heading = styled.h1`
padding:3rem 0;
text-align:center;
`

const Container = styled.section`
background-color:#94B3FD;
padding:0 0 2rem 0;`

const UserExp = () => {
  return (
    <>
    <Container id='2'>
    <Heading className='Section3'>React Learning Curve</Heading>
        <ResponsiveContainer width="100%" aspect={3}>
        <AreaChart
          width={500}
          height={400}
          data={data}
          margin={{
            top: 10,
            right: 30,
            left: 0,
            bottom: 0,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" stroke="#16181d" />
          <YAxis  stroke="#16181d"/>
          <Tooltip />
          <Area type="monotone" dataKey="progress" stroke="#425F57" fill="#A8E890" />
        </AreaChart>
      </ResponsiveContainer>
      </Container>
    </>
  )
}
const data = [
  {
    name: 'Day 1',
    progress: 1,
  },
  {
    name: 'Month 1',
    progress: 600,
  },
  {
    name: 'Month 2',
    progress: 1300,
  },
  {
    name: 'Month 3',
    progress: 1700,
  },
  {
    name: 'Month 4',
    progress: 2000,
  }
];

export default UserExp
